.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 10px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
}

.product > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product__info {
    height: 200px;
    margin-bottom: 15px;
}

.product__price {
    margin-top: 5px;
}

.product > button {
    background: #f0c14b;
    border: 1px solid;
    border-radius: 3px;
    margin-top: 10px;
    border-color: #a88734 #9c73e1 #846a29;
    color: #111;
    font-size: 1rem;
    cursor: pointer;
}

.product > button:hover {
    background-color: #111111;
    color: #ffffff;
    border-color: black;
}